.#{$op-prefix}form-text {
    font-size: 13px;
    line-height: 1.5;
    margin: 12px 0;
    color: $color--opui-gray500;
}

.opui-form-control:focus {
    border-color: $color--opui-gray900;
    box-shadow: none;
}

.#{$op-prefix}opui-form form label, .#{$op-prefix}form-group label, label.#{$op-prefix}form-label {
    font-size: 15px;
    color: $color--opui-black;
    padding: 11px 0;
    margin-bottom: 0.5rem;
    display: inline-block;
}

.#{$op-prefix}form-control {
    font-size: 16px;
    height: auto;
    line-height: 100%;
    padding: 1rem 1.5rem;
}

.#{$op-prefix}form-check-input {
    font-size: 18px;
    width: 15px;
    height: 15px;

    &:checked::before {
        margin: -1px 0 0 -2px;
        height: 16px;
        width: 16px;
    }
}
